'use client';

import React, { useEffect, useState } from 'react';
import { useParams } from 'next/navigation';
import Seo from '../components/Seo';
import BlogPost from '../components/blog/BlogPost';
import { BlogPost as BlogPostType, blogPosts } from '../data/blogPosts';
import NotFound from './NotFound';

const BlogPostPage: React.FC = () => {
  const params = useParams<{ slug?: string | string[] }>();
  const slugParam = params?.slug;
  const slug = Array.isArray(slugParam) ? slugParam[0] : slugParam;
  const [post, setPost] = useState<BlogPostType | null>(null);
  const [status, setStatus] = useState<'loading' | 'ready' | 'error'>('loading');

  useEffect(() => {
    const fetchPost = async () => {
      if (!slug) return;
      try {
        setStatus('loading');
        const baseUrl = process.env.NEXT_PUBLIC_API_BASE_URL || '';
        const response = await fetch(`${baseUrl}/api/blog/posts/${slug}`);
        const data = await response.json();
        if (response.ok && data?.post) {
          setPost(data.post);
          setStatus('ready');
          return;
        }

        const fallback = blogPosts.find((item) => item.slug === slug);
        if (fallback) {
          setPost(fallback);
          setStatus('ready');
          return;
        }

        throw new Error(data?.error || 'Post not found.');
      } catch (error) {
        const fallback = blogPosts.find((item) => item.slug === slug);
        if (fallback) {
          setPost(fallback);
          setStatus('ready');
        } else {
          setStatus('error');
        }
      }
    };

    fetchPost();
  }, [slug]);

  if (status === 'error') {
    return <NotFound />;
  }

  if (status !== 'ready' || !post) {
    return (
      <section className="bg-white text-black pt-32 pb-20 font-body-alt">
        <div className="page-container">
          <p className="text-gray-500 uppercase tracking-[0.3em] text-xs">Loading</p>
          <h1 className="mt-4 text-3xl md:text-5xl font-heading">Loading article...</h1>
        </div>
      </section>
    );
  }

  const baseUrl =
    process.env.NEXT_PUBLIC_SITE_URL ||
    (typeof window === 'undefined' ? '' : window.location.origin);
  const blogJsonLd = {
    '@context': 'https://schema.org',
    '@type': 'BlogPosting',
    headline: post.title,
    description: post.metaDescription || post.excerpt,
    image: [post.featuredImage.src],
    datePublished: post.date,
    dateModified: post.date,
    author: {
      '@type': 'Organization',
      name: post.author,
    },
    publisher: {
      '@type': 'Organization',
      name: 'WebOrbitSolution',
      logo: {
        '@type': 'ImageObject',
        url: `${baseUrl}/favicon.png`,
      },
    },
    mainEntityOfPage: {
      '@type': 'WebPage',
      '@id': `${baseUrl}/blog/${post.slug}`,
    },
  };

  return (
    <>
      <Seo
        title={post.metaTitle ? `${post.metaTitle} | WebOrbitSolution` : `${post.title} | WebOrbitSolution`}
        description={post.metaDescription || post.excerpt}
        path={`/blog/${post.slug}`}
        image={post.featuredImage.src}
        type="article"
        jsonLd={blogJsonLd}
      />
      <BlogPost post={post} />
    </>
  );
};

export default BlogPostPage;
